import React from "react";
import { Card, CardContent, CardHeader} from "../components/ui/card";
import { Button } from "../components/ui/button";
import { Badge } from "../components/ui/badge";
import { Trophy, Target, BookOpen, Clock, Users, ArrowRight } from "lucide-react";

const exams = [
  {
    id: 1,
    name: "JEE Main & Advanced",
    description: "Joint Entrance Examination for Engineering",
    category: "Engineering",
    difficulty: "High",
    duration: "3 hours",
    attempts: "2 per year",
    subjects: ["Physics", "Chemistry", "Mathematics"],
    color: "from-blue-500 to-indigo-600"
  },
  {
    id: 2,
    name: "NEET",
    description: "National Eligibility cum Entrance Test for Medical",
    category: "Medical",
    difficulty: "High",
    duration: "3 hours 20 minutes",
    attempts: "1 per year",
    subjects: ["Physics", "Chemistry", "Biology"],
    color: "from-green-500 to-emerald-600"
  },
  {
    id: 3,
    name: "CAT",
    description: "Common Admission Test for MBA",
    category: "Management",
    difficulty: "High",
    duration: "3 hours",
    attempts: "1 per year",
    subjects: ["Quantitative Ability", "Verbal Ability", "Data Interpretation"],
    color: "from-purple-500 to-violet-600"
  },
  {
    id: 4,
    name: "GATE",
    description: "Graduate Aptitude Test in Engineering",
    category: "Engineering",
    difficulty: "High",
    duration: "3 hours",
    attempts: "1 per year",
    subjects: ["Technical Subject", "General Aptitude", "Engineering Mathematics"],
    color: "from-orange-500 to-red-600"
  },
  {
    id: 5,
    name: "CLAT",
    description: "Common Law Admission Test",
    category: "Law",
    difficulty: "Medium",
    duration: "2 hours",
    attempts: "1 per year",
    subjects: ["English", "General Knowledge", "Legal Reasoning", "Logical Reasoning", "Quantitative Techniques"],
    color: "from-teal-500 to-cyan-600"
  },
  {
    id: 6,
    name: "NDA",
    description: "National Defence Academy Examination",
    category: "Defence",
    difficulty: "Medium",
    duration: "4 hours 30 minutes",
    attempts: "2 per year",
    subjects: ["Mathematics", "General Ability Test"],
    color: "from-red-500 to-pink-600"
  }
];

export default function CompetitiveExams() {
  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-br from-yellow-50 to-orange-50 py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <div className="inline-flex items-center px-4 py-2 bg-yellow-100 text-yellow-800 rounded-full text-sm font-medium mb-6">
              <Trophy className="w-4 h-4 mr-2" />
              Competitive Excellence
            </div>
            
            <h1 className="text-4xl lg:text-5xl font-bold text-gray-900 mb-6">
              Competitive
              <span className="text-transparent bg-clip-text bg-gradient-to-r from-yellow-600 to-orange-600">
                {" "}Exam Prep
              </span>
            </h1>
            <p className="text-xl text-gray-600 mb-8 max-w-3xl mx-auto">
              Master India's most competitive exams with our comprehensive preparation materials, 
              expert guidance, and proven strategies for success.
            </p>
            
            <div className="grid grid-cols-2 md:grid-cols-4 gap-8 max-w-2xl mx-auto">
              <div className="text-center">
                <div className="text-2xl font-bold text-yellow-600">15+</div>
                <div className="text-sm text-gray-600">Exams Covered</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-orange-600">95%</div>
                <div className="text-sm text-gray-600">Success Rate</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-red-600">10K+</div>
                <div className="text-sm text-gray-600">Practice Questions</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-purple-600">24/7</div>
                <div className="text-sm text-gray-600">Support</div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Exams Grid */}
      <section className="py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
            {exams.map((exam) => (
              <Card key={exam.id} className="group overflow-hidden hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1">
                {/* Header with Gradient */}
                <div className={`h-24 bg-gradient-to-r ${exam.color} relative overflow-hidden`}>
                  <div className="absolute inset-0 bg-black/10"></div>
                  <CardHeader className="relative h-full flex items-center justify-center">
                    <Trophy className="w-8 h-8 text-white" />
                  </CardHeader>
                </div>
                
                <CardContent className="p-6">
                  <div className="flex items-start justify-between mb-4">
                    <Badge variant="secondary" className="bg-gray-100 text-gray-700">
                      {exam.category}
                    </Badge>
                    <Badge 
                      variant="outline" 
                      className={exam.difficulty === "High" ? "border-red-500 text-red-700" : "border-yellow-500 text-yellow-700"}
                    >
                      {exam.difficulty}
                    </Badge>
                  </div>
                  
                  <h3 className="text-xl font-bold text-gray-900 mb-2">
                    {exam.name}
                  </h3>
                  
                  <p className="text-gray-600 mb-4">
                    {exam.description}
                  </p>
                  
                  <div className="space-y-2 mb-4 text-sm text-gray-600">
                    <div className="flex items-center">
                      <Clock className="w-4 h-4 mr-2" />
                      Duration: {exam.duration}
                    </div>
                    <div className="flex items-center">
                      <Target className="w-4 h-4 mr-2" />
                      Attempts: {exam.attempts}
                    </div>
                  </div>
                  
                  <div className="mb-4">
                    <h4 className="text-sm font-semibold text-gray-900 mb-2">Key Subjects:</h4>
                    <div className="flex flex-wrap gap-1">
                      {exam.subjects.slice(0, 2).map((subject, index) => (
                        <Badge key={index} variant="outline" className="text-xs">
                          {subject}
                        </Badge>
                      ))}
                      {exam.subjects.length > 2 && (
                        <Badge variant="outline" className="text-xs">
                          +{exam.subjects.length - 2} more
                        </Badge>
                      )}
                    </div>
                  </div>
                  
                  <Button className={`w-full bg-gradient-to-r ${exam.color} hover:opacity-90 text-white`}>
                    Start preparation
                    <ArrowRight className="ml-2 h-4 w-4" />
                  </Button>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}